import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import beanBackground from "../resource/img/bean-bg.png";

const useStyles = makeStyles((theme) => ({
	box: {
		border: "1px solid #46241e",
		backgroundColor: "#ffffff",
		margin: "2em 0",
	},
	cell: {
		border: "1px solid #46241e",
		padding: "0.6em",
		color: "#46241e",
	},
	dl: {
		display: "flex",
		flexWrap: "wrap",
		alignItems: "baseline",
		margin: 0,
		fontSize: "1.1em",
	},
	dt: {
		flexGrow: 0,
		fontFamily: "'Josefin Sans', sans-serif",
		fontWeight: 700,
		fontSize: "0.8em",
		letterSpacing: "2px",
	},
	dd: {
		flexGrow: 1,
		fontFamily: "'Josefin Slab', serif",
		fontStyle: "italic",
		fontWeight: 600,
		marginLeft: "0.75em",
	},
}));

const FarmerLayout = ({ location, title, children }) => {

	return (
		<Layout location={location} title={title}>
			<div
				style={{
					display: `flex`,
					flexDirection: `column`,
					minHeight: `100vh`,
					backgroundImage: `url(${beanBackground})`,
					backgroundRepeat: "no-repeat",
					backgroundPosition: "0px 360px",
					backgroundSize: "cover",
				}}
				className="farmerLayout"
			>
				<main
					style={{
						flexGrow: 1,
						maxWidth: "100%",
						marginLeft: `auto`,
						marginRight: `auto`,
						padding: `0 1.5em`,
					}}
				>
					{children}
				</main>
			</div>
		</Layout>
	)
}

const InfoCard = (props) => {
	const classes = useStyles();

	const { farmer } = props;

	return (
		<div className={classes.box}>
			<Grid container spacing={0}>
				<Grid className={classes.cell} item xs={12}>
					<dl className={classes.dl}>
						<dt className={classes.dt}>LOCATION:</dt>
						<dd className={classes.dd}>{farmer.frontmatter.location}<br/>{farmer.frontmatter.coordinateString}</dd>
					</dl>
				</Grid>
				<Grid className={classes.cell} item xs={6}>
					<dl className={classes.dl}>
						<dt className={classes.dt}>ALTITUDE:</dt>
						<dd className={classes.dd}>{farmer.frontmatter.altitude.toLocaleString()} ft</dd>
					</dl>
				</Grid>
				<Grid className={classes.cell} item xs={6}>
					<dl className={classes.dl}>
						<dt className={classes.dt}>PROCESS:</dt>
						<dd className={classes.dd}>{farmer.frontmatter.process}</dd>
					</dl>
				</Grid>
				<Grid className={classes.cell} item xs={12}>
					<dl className={classes.dl}>
						<dt className={classes.dt}>FLAVOR:</dt>
						<dd className={classes.dd}>{farmer.frontmatter.flavor}</dd>
					</dl>
				</Grid>
				{farmer.otherCrops !== "" &&
					<Grid className={classes.cell} item xs={12}>
						<dl className={classes.dl}>
							<dt className={classes.dt}>OTHER CROPS:</dt>
							<dd className={classes.dd}>{farmer.frontmatter.otherCrops}</dd>
						</dl>
					</Grid>
				}
			</Grid>
		</div>	
	);
};

const FarmerTemplate = ({ data, pageContext, location }) => {
  const farmer = data.markdownRemark;
  const siteTitle = data.site.siteMetadata.title;

  return (
    <FarmerLayout location={location} title={siteTitle}>
      <SEO
        title={farmer.frontmatter.name}
        description={farmer.frontmatter.description || farmer.excerpt}
      />
      <article>
        <header>
        </header>
        <section dangerouslySetInnerHTML={{ __html: farmer.html }} />
		{farmer.frontmatter.altitude && 
			<InfoCard farmer={farmer} />
		}
        <footer>
        </footer>
      </article>
    </FarmerLayout>
  )
}

export default FarmerTemplate;

export const pageQuery = graphql`
  query FarmerBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
		name
		tags
		coordinates
		location
		coordinateString
		altitude
		process
		flavor
		otherCrops
      }
    }
  }
`
